import './Detail_ask_write.css';
import { useState } from 'react';
import Loading from '../../../components/Loading';
import Error from '../../../components/Error';
import { SERVER_RESOURCE } from '../../../../model/server-config';
import { useDispatch, useSelector } from 'react-redux';
import { changeAlert } from '../../../redux/basic/actions';
import { api } from '../../../../model/model';


const Detail_ask_write = ({ item, setRefresh, refresh, itemAskClick }) => {
    const dispatch = useDispatch();
    const user = JSON.parse(sessionStorage.getItem('userinfo'));
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [askBoxClose, setAskBoxClose] = useState(true);
    const [checked, setChecked] = useState(false);
    const [ask, setAsk] = useState({
        item_code: item.code,
        writer: 'manager1',
        title: '',
        contents: '',
        reply: null,
        password: null,
        type: ''
    })

    const changeAsk = (event) => {
        setAsk((ask) => ({
            ...ask,
            [event.target.name]: event.target.value
        }))
    }

    const changeChecked = () => {
        setChecked(!checked)
    }
    const submitAsk = async () => {
        await api('/itemask/merge', 'post', ask, user.token)
            .then(res => {
                setLoading(false);
                dispatch(changeAlert({
                    title: ' !',
                    content: ``,
                    time: 3,
                    style: {
                        top: '50%',
                        left: 'calc(50% - 150px)',
                        zIndex: 5
                    }
                }));
            }).catch(err => {
                console.log(err.message)
                setLoading(false);
                setError(true);
            });
        setRefresh(!refresh);
        itemAskBoxClose();
    }

    const itemAskBoxClose = () => {
        setAskBoxClose(!askBoxClose);
        itemAskClick();
    }

    const maxLengthCheck = (event) => {
        if (event.target.value.length > event.target.maxLength) {
            event.target.value = event.target.value.slice(0, event.target.maxLength);
        }
    }

    if (loading) return <Loading />
    if (error) return <Error />

    return (
        <>
            {askBoxClose &&
                <div id="itemAskForm">
                    <div>
                        <div id="itemAskBox">
                            <h4 > </h4>
                            <div id="itemAskTop">
                                <div id='itemAskImg'>
                                    <img src={SERVER_RESOURCE + `/img/itemImg/${item.code}_2.jpg`} alt="" />
                                </div>
                                <div id='itemTitle'>
                                    {item.name}
                                    <div id='itemAskPrivacyContainer'>
                                        <div id='itemAskPrivacyBox'>
                                            <label id="itemAskPrivacy">
                                                <input type="checkbox" name="privacyBox" onChange={changeChecked} checked={checked} />
                                                 
                                            </label>
                                            {checked && <input type='number' id='passwordAsk' name='password' value={ask.password} onChange={changeAsk} onInput={maxLengthCheck} maxlength="4" placeholder='4 ' />}
                                        </div>
                                    </div>
                                </div>

                            </div>
                            <div id="itemAskBottom">
                                <div id='itemAskTitle'>
                                    <div></div>
                                    <textarea onChange={changeAsk} name='title' type="text" placeholder=" " value={ask.title}></textarea>
                                </div>
                                <div id='itemAskContent'>
                                    <div></div>
                                    <textarea id='askWriteTag_detail' name='contents' type="text" value={ask.contents} onChange={changeAsk}
                                        placeholder="    .
                                1.    2~3 .
                                2.             .
                                3. , (//)    1:1  ">
                                    </textarea>
                                </div>
                            </div>

                            <div id="itemAskButton">
                                <button onClick={itemAskBoxClose} id="itemAskBoxCancle"></button>
                                <button onClick={submitAsk} id="itemAskBoxEnter"
                                    style={{
                                        backgroundColor: ask.title.length > 0 && ask.contents.length > 0 ? '#9b1b20' : '#e0e0e0',
                                        color: ask.title.length > 0 && ask.contents.length > 0 ? '#fff' : 'black'
                                    }}></button>
                            </div>
                        </div>
                        <div onClick={itemAskBoxClose} id="itemAskBoxClose"><i className="fa-solid fa-xmark"></i></div>
                    </div>
                </div>
            }
        </>

    );
}

export default Detail_ask_write;