
import './SideMenu.css'
import { Link } from 'react-router-dom';


const SideMenu = ({ openSideBar, sideBarOpen }) => {
    const path = window.location.pathname;
    const location = path.substring(path.lastIndexOf('/') + 1);

    const getLocation = () => {
        switch (location) {
            case 'ask':
                return ' ';
            case 'select':
                return ' '
            case 'chatbot':
                return ' '
        }
    }

    return (
        <>
            <div id="topBarA" style={{ paddingLeft: sideBarOpen ? '95px' : '15px' }}>
                <i className="fa-solid fa-house"></i>
                &nbsp;&nbsp;  &nbsp;&nbsp;
                <span>
                    <i className="fa-solid fa-chevron-right"></i>&nbsp;&nbsp;{getLocation()}
                </span>


            </div>
            <div id="sideBar" style={{ transform: sideBarOpen ? 'translateX(0%)' : 'translateX(-100%)' }}>
                <div></div>
                <ul>
                    <li>
                        <Link to="/admin"><i className="fa-solid fa-house"></i><br /> </Link>
                    </li>
                    {/* <li>
                        <Link to="/admin/graph"><i className="fa-solid fa-square-poll-vertical"></i><br /></Link>
                    </li> */}
                    <li>
                        <Link to="/admin/select"><i className="fa-solid fa-magnifying-glass"></i><br /> </Link>
                    </li>
                    <li>
                        <Link to="/admin/chatbot"><i className="fa-solid fa-bullhorn"></i><br /> </Link>
                    </li>
                    <li>
                        <Link to="/admin/ask"><i className="fa-solid fa-comment-dots"></i><br /> </Link>
                    </li>
                </ul>
                <div id="sideBarButton" onClick={openSideBar}>
                    <i className="fa-solid fa-chevron-right" style={{ transform: sideBarOpen ? 'rotateY(180deg)' : 'rotateY(0deg)' }}></i>
                </div>
            </div>
        </>
    );
}

export default SideMenu;