import './ChatBotBox.css';
import { useState, useRef, useEffect } from 'react';
import { SERVER_RESOURCE } from '../../../model/server-config';
import { api } from '../../../model/model'
import Error from './../../components/Error';
import Loading from './../../components/Loading';

const ChatBotBox = ({
    /* admin   props */
    amount, // admin   
    admin_root, //   
    user,
    getdata,
    changeShowChatbot, // admin     
    /* index   props */
    setShowChatbot, // index      
}) => {
    const [refresh, setRefresh] = useState(false);
    /*   sessionStorage  */
    const userinfo = JSON.parse(sessionStorage.getItem('userinfo'));

    /* axios  //  */
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);

    /*    form */
    const [text, setText] = useState({
        room_seq: admin_root,
        content: '',
        writer: JSON.parse(sessionStorage.getItem('userinfo')).id || '', // sessionStorage  id
    });

    /* text form   */
    const changeContent = (event) => {
        setText((prev) => ({
            ...prev,
            content: event.target.value
        }))
    }
    /*   */
    const startChat = (event) => {
        console.log(event.target.closest('div').children[1].innerText)
        api('/chat/makeroom', 'post', { type: event.target.closest('div').children[1].innerText }, userinfo.token)
            .then(res => {
                console.log(res.data)
                setText((prev) => ({
                    ...prev,
                    room_seq: res.data.seq
                }))
            })
            .catch(err => {
                console.log(`startChat Error : ${err.message}`)
            })
    }

    const [userChatRoom, setUserChatRoom] = useState(null);
    useEffect(() => {
        api('/chat/selectroom', 'get', null, userinfo.token)
            .then(res => {
                setUserChatRoom(res.data)
            })
            .catch(err => {
                console.log(err.message)
            })
    }, [])

    /*   */
    const endChat = () => {
        const data = {
            seq: text.room_seq,
            type: text.type,
            ing: 2
        }

        api('/chat/makeroom', 'post', data, userinfo.token)
            .then(res => {
                if (admin_root) {
                    getdata(res.data)
                    changeShowChatbot(admin_root)
                } else
                    setShowChatbot(false)
            })
            .catch(err => {
                console.log(`startChat Error : ${err.message}`)
            })
    }

    /*      */
    const [messageAll, setMessageAll] = useState(null);
    const getMessageAll = async (root) => {
        setLoading(true);
        await api(`/chat/selectmessage?room_seq=${root}`, 'get', null, userinfo.token)
            .then((res) => {
                setLoading(false);
                setMessageAll(res.data);
                setText((prev) => ({
                    ...prev,
                    room_seq: res.data[0].room_seq,
                    content: ''
                }));
            })
            .catch((err) => {
                setLoading(false);
                setError(true);
            });
    }

    /* admin       */
    useEffect(() => {
        admin_root && getMessageAll(admin_root);
        text.room_seq && getMessageAll(text.room_seq);
    }, [refresh])

    /*   input   */
    const inputBox = useRef(null);

    /*    server DB    merge
         root    select  api   */
    const insertMessage = async () => {
        if (!text.content.trim()) {
            return;
        }
        setLoading(true);
        await api('/chat/insertmessage', 'post', text, userinfo.token)
            .then(res => {
                setLoading(false);
                setMessageAll(res.data);
                setText((prev) => ({
                    ...prev,
                    content: ''
                }))
                if (inputBox.current) inputBox.current.focus();
            }).catch(err => {
                setLoading(false);
                setError(true);
                console.log(`insertMessage Error : ${err.message}`)
            });
        if (admin_root) {
            await api('/chat/makeroom', 'post', {
                seq: admin_root,
                user: user,
                admin: userinfo.id,
                ing: 1
            }, userinfo.token)
                .then(res => {
                    getdata(res.data)
                })
        }
    }

    /* input     userChatbot  handler()  */
    const handleKeyUp = async (event) => {
        if (event.key === 'Enter' && text.content.trim() !== '') {
            await insertMessage();
        }

    };

    /*       */
    const chatBox = useRef(null);
    useEffect(() => {
        if (chatBox.current)
            chatBox.current.scrollTop = chatBox.current.scrollHeight;
    }, [messageAll])


    return (
        <div id='chatBotBox' style={{ height: amount == 2 ? 'calc(50% - 10px)' : '100%' }}>
            <div id='chatBox' ref={chatBox}>
                <h3>
                    {(admin_root || text.type) && <div onClick={() => endChat()} className='end'></div>}
                    {admin_root ? ` : ${admin_root}` : ' '}
                    {admin_root && <div className="close" onClick={() => changeShowChatbot(admin_root)}><i className="fa-solid fa-xmark"></i></div>}
                    {setShowChatbot && <div onClick={() => setShowChatbot()} className="close"><i className="fa-solid fa-xmark"></i></div>}
                    <div className="refresh" onClick={() => setRefresh(!refresh)}><i style={{ transform: refresh ? 'rotate(0deg)' : 'rotate(360deg)' }} className="fa-solid fa-arrows-rotate"></i></div>
                </h3>

                {
                    !messageAll && loading && <Loading />
                }

                {
                    !messageAll && error && <Error />
                }
                {
                    !admin_root && !messageAll && !error && !loading &&
                    <>
                        <div id="chatBotTitle">
                            <img src={SERVER_RESOURCE + "/img/logo2.png"} />
                            <p>
                                {userinfo.username} , .<br />
                                 ?<br />
                                <span>  ,  .</span>
                            </p>
                        </div>
                        {!text.room_seq &&
                            <div id="openQuestion">
                                <h4></h4>
                                <div id="openQuestionBox">
                                    <div onClick={(event) => startChat(event)}>
                                        <i className="fa-solid fa-truck"></i>
                                        <span></span>
                                    </div>
                                    <div onClick={(event) => startChat(event)}>
                                        <i className="fa-solid fa-cubes"></i>
                                        <span></span>
                                    </div>
                                    <div onClick={(event) => startChat(event)}>
                                        <i className="fa-solid fa-gift"></i>
                                        <span></span>
                                    </div>
                                </div>
                            </div>
                        }
                        {!text.room_seq && userChatRoom && userChatRoom.length > 0 &&
                            <div id="beforechat">
                                <h4> </h4>
                                {userChatRoom.map((e, i) =>
                                    <div className='beforechatRoom' key={i}>
                                        <span>{`${new Date(e.regdate).getFullYear()}.${new Date(e.regdate).getMonth()}.${new Date(e.regdate).getDate()} ${new Date(e.regdate).getHours()}`}</span>
                                        <div onClick={() => getMessageAll(e.seq)}></div>
                                    </div>
                                )}
                            </div>
                        }
                    </>
                }

                <div id='messageBox'>
                    {/* {messageAll && messageAll.map((e, i) => <span>{new Date().toLocaleTimeString([], { hour: 'numeric', minute: 'numeric' })}</span>)} */}
                    {messageAll ?
                        admin_root ?
                            messageAll.slice(1).map((e, i) => <p className={e.writer === userinfo.id ? 'myChat' : 'otherChat'} key={i}>{e.content}<br></br><span>{new Date(e.regdate).getHours()} {new Date(e.regdate).getMinutes()}</span></p>)
                            :
                            messageAll.map((e, i) => <p className={e.writer === userinfo.id ? 'myChat' : 'otherChat'} key={i}>{e.content}<br></br><span>{new Date(e.regdate).getHours()} {new Date(e.regdate).getMinutes()}</span></p>)
                        :
                        null
                    }

                </div>

            </div>
            <div id="chatBotTextBox">
                <input type="text" readOnly={!text.room_seq} placeholder={text.type ? " ." : " ."} value={text.content} onChange={(event) => changeContent(event)} ref={inputBox}
                    onKeyUp={handleKeyUp} />
                <button onClick={insertMessage} ></button>
            </div>
        </div >
    );
}

export default ChatBotBox;