import './Detail_descript.css';

import { useRef, useState, useEffect, useMemo } from 'react';
import { makeComa, makeDiscountPrice } from '../../../components/MathFunction';
import Loading from '../../../components/Loading';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { getUserCart, setUserCartStorage } from '../../../redux/userCart/action';
import { SERVER_RESOURCE } from '../../../../model/server-config';
import Cart_alert from './Cart_alert';
import { setUserBuyStorage } from '../../../redux/userBuy/actions';
import { api } from '../../../../model/model';

const Detail_descript = ({ item }) => {

    /* REDUX */
    const dispatch = useDispatch();

    const userinfo = JSON.parse(sessionStorage.getItem('userinfo'));
    const userCart = useSelector(state => state.userCart.data);

    /* State */
    const [inputCountValue, setInputCountValue] = useState(1);
    const [introItem, setIntroItem] = useState(false)
    const [gotoCart, setGotoCart] = useState(false);
    const [loading, setLoading] = useState(false);
    const currentDate = new Date();
    const weekdays = ['', '', '', '', '', '', '', ''];
    const date = currentDate.getDate();
    const hour = currentDate.getHours();
    const dayOfWeek = currentDate.getDay();

    let inputCountRef = useRef(null)
    let priceRef = useRef(null)

    useEffect(() => {
        // if (userinfo && userinfo.login) {
        //     dispatch(getUserCart(`/usercart/merge?code=${item.code}`, 'get', null, userinfo.token))
        // } else {
        //     let cartArray = JSON.parse(localStorage.getItem('cart')) || [];
        //     let itemIndex = cartArray.findIndex(e => e.code == item.code);
        //     if (itemIndex !== -1) {
        //         cartArray[itemIndex].amount += 0;
        //     } else {
        //         cartArray.push({ code: item.code, amount: 0 });
        //     }
        //     dispatch(setUserCartStorage(cartArray));
        // }
    }, [])

    const changeMainImg = (event) => {
        let ele = event.target.closest('div');
        ele.style.opacity = 1;
        ele.parentNode.previousElementSibling.children[0].src = ele.children[0].src;
        for (let i = 0; i < ele.parentNode.childElementCount; i++) {
            if (ele.parentNode.children[i] != ele) {
                ele.parentNode.children[i].style.opacity = '0.5';
            }
        }
        window.scrollTo(0, 235);
    }

    const clickInputCount = (type) => {
        let value = inputCountRef.current.value

        if ("-" === type) {
            if (value > 0) {
                value--;
                setInputCountValue(value);
            }
        }
        else {
            value++
            setInputCountValue(value);
        }
    }

    const showItemDetail = () => {
        setIntroItem(!introItem)
    }

    const changeInputCount = (event) => {
        setInputCountValue(event.target.value);
    }


    const addCart = () => {
        setLoading(true);
        setGotoCart(!gotoCart);

        if (userinfo && userinfo.login) {
            const formData = {
                code: item.code,
                amount: inputCountValue,
                id: userinfo.id
            };
            const ar = [];
            ar.push(formData);
            dispatch(getUserCart('/usercart/merge', 'post', ar, userinfo.token))
        } else {
            let cartArray = JSON.parse(localStorage.getItem('cart')) || [];
            const itemIndex = cartArray.findIndex(cartItem => cartItem.code == item.code);
            if (itemIndex !== -1) {
                cartArray[itemIndex].amount += +inputCountValue;
            } else {
                cartArray.push({ code: item.code, amount: +inputCountValue });
            }
            dispatch(setUserCartStorage(cartArray));
        }
        setLoading(false);
    };

    const addBuy = async () => {
        const response = await api(`/item/selectwhere?column=item.code&keyword=${item.code}`, 'get')
        dispatch(setUserBuyStorage([{ ...response.data[0], amount: +inputCountValue }]))
    }

    return (
        <div id="itemDetailBox" className="container">
            <div id="imgBox">
                <div id="imgBoxImg">
                    <img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_1.jpg`} alt={`${item.name} `} />
                </div>
                <div id="imgBoxImgList">
                    <div onClick={changeMainImg}><img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_1.jpg`} alt={`${item.name} `} /></div>
                    <div onClick={changeMainImg}><img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_2.jpg`} alt={`${item.name} `} /></div>
                    <div onClick={changeMainImg}><img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_3.jpg`} alt={`${item.name} `} /></div>
                    <div onClick={changeMainImg}><img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_4.jpg`} alt={`${item.name} `} /></div>
                </div>
            </div>

            <div id="itemDetail">
                <div id="itemDetailTitle">
                    <div id="itemDelivery">{item.event}</div>
                    <div id="itemName">{item.name}</div>
                    <div id="itemAccount">   </div>
                    {
                        item.discount ?
                            <>
                                <span id="itemSale">{item.discount}<span>%</span></span>
                                <div id="itemPrice">{makeComa(item.price)}</div>
                                <div id="itemSalePrice">{makeComa(makeDiscountPrice(item.price, item.discount))}</div>
                            </>
                            :
                            <div id="itemSalePrice">{makeComa(item.price)}</div>
                    }
                </div>
                <div></div>
                <div>{item.delivery ? makeComa(item.delivery) + ' ' : ''}<br />
                    {
                        hour < 15 ?
                            `(15     "${date + 1} (${weekdays[dayOfWeek + 1]})"  )`
                            : `(23      "${date + 2} (${weekdays[dayOfWeek + 2]})"  )`
                    }
                </div>
                <div></div>
                <div>{item.brand}</div>
                <div></div>
                <div>{item.storage}</div>
                <div></div>
                <div>{item.packing}</div>
                <div>/</div>
                <div>{item.weight}g</div>
                <div></div>
                <div>  180    .</div>
                <div id="itemSelect">
                    <div> </div>
                    <div id="countBox">
                        <button onClick={() => clickInputCount("-")}><i className="fa-solid fa-minus"></i></button>
                        <input onChange={changeInputCount} id="inputCount" type="number" value={inputCountValue} ref={inputCountRef} />
                        <button onClick={() => clickInputCount("+")}><i className="fa-solid fa-plus"></i></button>
                    </div>
                    <div id="priceBox">
                        <div id="priceAmount"> &nbsp; : &nbsp;<span ref={priceRef}>{makeComa(makeDiscountPrice(item.price, item.discount) * inputCountValue)}</span></div>
                        <div onClick={gotoCart ? null : addCart} id="gotocart"> </div>
                        <Link to="/home/buy" id="gotobuy" onClick={() => addBuy()}></Link>
                    </div>
                </div>
                {
                    loading ?
                        <Loading />
                        :
                        gotoCart ?
                            <Cart_alert name={item.name} setGotoCart={setGotoCart} />
                            :
                            null
                }
            </div>

            <ul id="detailClick" className="container">
                <li id="detailButton"><a onClick={() => setIntroItem(true)} href="#introItem1"></a></li>
                <li><a onClick={() => setIntroItem(true)} href="#introItem2"></a></li>
                <li><a href="#reviewBoardBox"></a></li>
                <li><a href="#askBoardBox"></a></li>
            </ul>
            <div id="introItem" className="container" style={{ height: introItem ? 'auto' : '500px' }}>
                <div id="introItem1" className="subTitle">
                    <hr />
                    <h4>  
                        <img src={SERVER_RESOURCE + `/img/logo3.png`} alt="  " />
                    </h4>
                    <hr />
                </div>
                <img src={SERVER_RESOURCE + `/img/itemImg/${item.code}_2.jpg`} alt={`${item.name} `} />
                <div id="introItem2" className="subTitle">
                    <hr />
                    <h4> 
                        <img src={SERVER_RESOURCE + `/img/logo4.png`} alt=" " />
                    </h4>
                    <hr />
                </div>
                <img src={SERVER_RESOURCE + `/img/itemImg/${item.code}_3.jpg`} alt={`${item.name} `} />
                <div className="subTitle">
                    <hr />
                    <h4> 
                        <img src={SERVER_RESOURCE + `/img/logo2.png`} alt="  " />
                    </h4>
                    <hr />
                </div>
                <img src={SERVER_RESOURCE + `/img/itemImg/${item.code}_4.jpg`} alt={`${item.name} `} />
            </div>
            {
                introItem ?
                    <div onClick={showItemDetail} id="introItemBtn">
                         <i class="fa-solid fa-chevron-up"></i>
                    </div>
                    :
                    <div onClick={showItemDetail} id="introItemBtn">
                         <i className="fa-solid fa-chevron-down"></i>
                    </div>
            }
        </div>
    );
}

export default Detail_descript;

