import { Link } from "react-router-dom";
import './FirstContainer.css';
import { SERVER_RESOURCE } from "../../../../model/server-config";

const FirstContainer = () => {
    return (
        <div id="firstContainer" className="container">
            <h3>
                <i className="fa-solid fa-star"></i>&nbsp;&nbsp;   &nbsp;&nbsp;<i className="fa-solid fa-star"></i>
            </h3>
            <div id="firstContainerButton">
                <div>
                    <Link>
                        <img src={SERVER_RESOURCE + "/img/index_bestSeller.png"} alt=" " className="categoryImg" /><br />
                    </Link>
                     
                </div>
                <div>
                    <Link to="/home/list?keyword=">
                        <img src={SERVER_RESOURCE + "/img/index_mealkit.png"} alt="" className="categoryImg" /><br />
                    </Link>
                    
                </div>
                <div>
                    <Link to="/home/list?keyword=">
                        <img src={SERVER_RESOURCE + "/img/index_food.png"} alt=" " className="categoryImg" /><br />
                    </Link>
                     
                </div>
                <div>
                    <Link>
                        <img src={SERVER_RESOURCE + "/img/index_menu.png"} alt=" " className="categoryImg" /><br />
                    </Link>
                     
                </div>
                <div>
                    <Link>
                        <img src={SERVER_RESOURCE + "/img/index_gift.png"} alt="" className="categoryImg" /><br />
                    </Link>
                    
                </div>
            </div>
        </div>
    );
}

export default FirstContainer;