const Admin_Chatbot_Row = ({
    room, //  
    showChatbot, //    
    changeShowChatbot //     
}) => {
    /*     component root filter */

    return (
        <ul>
            <li>{room.seq}</li>
            <li>{room.type}</li>
            <li style={{
                backgroundColor: room.ing != 0 ?
                    room.ing == 1 ?
                        'green'
                        :
                        'grey'
                    :
                    '#9B1B30'
            }}>
                {room.ing == 0 && ''}
                {room.ing == 1 && ''}
                {room.ing == 2 && ''}
            </li>
            <li><span>{new Date(room.regdate).getHours()} {new Date(room.regdate).getMinutes()}</span></li>
            <li>{room.user}</li>
            <li>{room.admin}</li>
            {showChatbot.includes(room.seq) ?
                <li style={{ backgroundColor: 'black' }} id="chtaBotING" onClick={() => changeShowChatbot(room.seq)} ></li>
                :
                <li id="chtaBotING" onClick={() => changeShowChatbot(room.seq)} ></li>
            }
        </ul>
    );
}

export default Admin_Chatbot_Row;