import './ItemListContainer.css'
import { useState } from "react";
import ItemBox from './../../components/ItemBox';
import ItemBox_vertical from './../../components/itemBox_vertical/ItemBox_vertical';
import PagingBox, { paging } from "../../components/PagingBox";
import { useDispatch } from 'react-redux';
import { setItemList } from '../../redux/itemList/actions';
import { SERVER_RESOURCE } from '../../../model/server-config';


const ItemListContainer = ({ itemList }) => {
    console.log('ItemListContainer ')
    let size = 16;
    const makeListSize = () => {
        if (window.matchMedia("(max-width : 1024px)").matches) {
            size = 15;
        } else {
            size = 16;
        }
        setLimit(size)
    }
    window.addEventListener('resize', makeListSize);

    const dispatch = useDispatch();
    const [sort, setSort] = useState('sales');
    const [currPage, setCurrPage] = useState(1);
    const [limit, setLimit] = useState(16);
    const [vertical, setVertical] = useState({
        vertical: false,
        infinite: true
    });

    const sortItemList = (event, list) => {
        // 1.   Column   onClick   id 
        // 2. list =   List
        let sortType = event.target.id;
        let sortedList;
        if (sortType.includes("D")) {
            sortType = sortType.replace("D", "")
            sortedList = list.sort((a, b) => {
                setCurrPage(1)
                setSort(sortType + "D");
                return b[sortType] - a[sortType];
            })
        } else {
            setSort(sortType);
            setCurrPage(1)
            sortedList = list.sort((a, b) => {
                return a[sortType] - b[sortType];
            })
        }
        dispatch(setItemList(sortedList));
    }

    return (
        <>
            <div id="listContainer">
                <div id="containerOption">
                    <ul id="listButton">
                        <li style={{ opacity: !vertical.vertical && '1' }} onClick={() => setVertical(() => ({ ...vertical, vertical: false }))}><div style={{ backgroundImage: `url(${SERVER_RESOURCE}/img/listbutton.png)` }}></div></li>
                        <li style={{ opacity: vertical.vertical && '1' }} onClick={() => setVertical(() => ({ ...vertical, vertical: true }))}><div style={{ backgroundImage: `url(${SERVER_RESOURCE}/img/listbutton.png)` }}></div></li>
                        <li style={{ opacity: !vertical.infinite && '1' }} onClick={() => setVertical(() => ({ ...vertical, vertical: !vertical.infinite }))}><div style={{ backgroundImage: `url(${SERVER_RESOURCE}/img/listbutton.png)` }}></div></li>
                    </ul>
                    <div id="total"> <span>{itemList ? itemList.length : '0'}</span> </div>
                    <div id="listOption">
                        <div id="sales" style={{ opacity: sort == "sales" ? '1' : '0.5' }} onClick={(event) => sortItemList(event, itemList)}></div>
                        <div id="price" style={{ opacity: sort == "price" ? '1' : '0.5' }} onClick={(event) => sortItemList(event, itemList)}></div>
                        <div id="priceD" style={{ opacity: sort == "priceD" ? '1' : '0.5' }} onClick={(event) => sortItemList(event, itemList)}></div>
                    </div>
                </div>
                {
                    vertical.vertical ?
                        (paging(itemList, currPage, size).map((e, i) => <ItemBox_vertical key={i} item={e} />))
                        :
                        (paging(itemList, currPage, size).map((e, i) => <ItemBox key={i} item={e} />))
                }
            </div>
            <PagingBox limit={limit} setLimit={setLimit} list={itemList} currPage={currPage} setCurrPage={setCurrPage} />
        </>
    );
}

export default ItemListContainer;

