import { Link } from 'react-router-dom';
import './itemBox.css';
import { makeComa } from './MathFunction';
import { SERVER_RESOURCE, SERVER_URL } from './../../model/server-config';

const ItemBox = ({ item }) => {
    return (
        <Link to={"/home/detail?code=" + item.code} className="itemBox">
            <div className="itemImg">
                <i className="fa-solid fa-cart-shopping"></i>
                <i className="fa-solid fa-magnifying-glass"></i>
                <img src={SERVER_RESOURCE + `/img/itemImg/${item.code < 10000 ? 'default' : item.code}_1.jpg`} alt={item.name} />
            </div>
            <div className="itemName">{item.name}</div>
            <div className="itemInfo">{item.brand}<br /></div>
            {
                item.discount ? (
                    <>
                        <p className="itemPriceB">{makeComa(item.price)}</p>
                        <p className="itemPrice">{makeComa(Math.round(item.price * (100 - item.discount) / 100))}</p>
                    </>
                ) : (
                    <p className="itemPrice">{makeComa(item.price)}</p>
                )
            }
            {
                item.delivery > 0 ? (
                    <div className="itemDelivery"><span>&nbsp;&nbsp;</span>{makeComa(item.delivery)}</div>
                ) : (
                    <div className="itemDelivery"></div>
                )
            }
            <div className="itemOption">
                {
                    item.delivery === 0 && (
                        <div className="itemOptionFreeDelivery"></div>
                    )
                }
                {
                    item.event_code != null && (
                        <div className="itemOptionEvent">{item.event_name}</div>
                    )
                }
            </div>
        </Link>
    );
}

export default ItemBox;
